  border-image-slice: 14 fill;
  border-image-width: .25em;
  padding: .625em .5em .5em;
}

nav, #content, footer > span, body > span {
  image-rendering: pixelated;
  border-image-source: url("Border Box.062adf39.png");
  border-image-slice: 16 fill;
  border-image-width: 1rem;
  border-image-outset: .75rem;
  margin: .75rem;
}

nav > *, #content > *, footer > span > *, body > span > * {
  image-rendering: initial;
}

a:focus, :focus {
  outline: var(--border-thin) solid #d83700;
  outline-offset: var(--border-thin);
}

.button:hover, #top-link:hover {
  filter: brightness(1.15);
}

:root {
  word-spacing: .125em;
  --border-thin: 2px;
  --border-med: 3px;
  font-family: Stardew Valley Stonks, Nunito, sans;
  line-height: 1.25;
}

body {
  min-width: min-content;
  min-height: 100vh;
  width: 100%;
  image-rendering: pixelated;
  background-image: url("Background.81ce682a.png");
  background-position: bottom;
  background-size: cover;
  background-attachment: fixed;
  flex-direction: column;
  align-items: center;
  margin: 0;
  display: flex;
  overflow-y: scroll;
}

body > span {
  width: max-content;
  margin: 1em auto;
  padding: 1em;
  font-size: 1.5rem;
  display: block;
}

body > * {
  box-sizing: border-box;
  width: 100%;
  image-rendering: initial;
}

header {
  max-width: 100vw;
  padding: 1.5em;
}

header > a {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1em 2em;
  display: flex;
}

footer {
  text-align: center;
  min-height: 15vh;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  gap: 1em;
  padding: 1em;
  font-size: 1.25rem;
  display: flex;
}

footer > span {
  padding: .625em .75em .5em;
}

@media (max-width: calc(3em + 428px)) {
  header > a > div {
    width: 100%;
    aspect-ratio: 428 / 84;
    justify-content: center;
    display: flex;
  }
}

img {
  -webkit-user-select: none;
  user-select: none;
}

a {
  color: #004d92;
}

a:visited {
  color: #8300bf;
}

dl {
  grid-template-columns: fit-content(13em) auto;
  gap: .5em;
  margin: 0;
  display: grid;
}

dt {
  text-align: right;
}

dd, h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
}

main {
  grid-template-columns: minmax(11.5rem, 1fr) minmax(33.5rem, 63.5rem) 1fr;
  justify-content: center;
  gap: 1rem;
  padding: 1rem;
  display: grid;
}

nav {
  width: -moz-fit-content;
  width: fit-content;
  height: min-content;
  justify-self: end;
  padding: 1rem;
  font-size: 1.25rem;
  position: sticky;
  top: 1.75rem;
}

#top-link {
  text-decoration: none;
  display: none;
  position: sticky;
  bottom: 1em;
}

#content {
  flex-flow: column;
  row-gap: 1em;
  padding: 0 1rem 1rem;
  display: flex;
}

section {
  flex-direction: column;
  row-gap: .5em;
  display: flex;
}

section > h2 {
  margin-top: .5em;
}

@media (max-width: 50rem) {
  main {
    flex-direction: column;
    align-items: center;
    row-gap: 1rem;
    display: flex;
  }

  nav {
    position: unset;
  }

  #top-link {
    display: block;
  }
}

@media (max-width: 40rem) {
  dl {
    flex-direction: column;
    row-gap: .25em;
    display: flex;
  }

  dt {
    text-align: unset;
    margin-top: .75em;
  }

  dt:first-child {
    margin-top: .5em;
  }

  dd {
    margin-left: 1em;
  }
}

ul {
  flex-direction: column;
  row-gap: .5em;
  padding-left: 1em;
  display: flex;
}

ul ul {
  margin-top: .5em;
}

nav > ul {
  padding: 0;
  list-style: none;
}

h1 {
  padding-top: .5em;
  font-size: 1.75rem;
}

h2 {
  font-size: 1.25rem;
}

blockquote {
  background: #fff0ba;
  border-radius: .5em;
