  row-gap: .5em;
  margin: .5em 0;
  padding: .5em .75em;
  display: flex;
}

dt {
  font-weight: bold;
}

figure {
  flex-direction: column;
  align-items: center;
  row-gap: .5em;
  margin: .5em 0;
  display: flex;
}

figure > img {
  max-width: 100%;
  height: auto;
  max-height: min(20rem, 50vh);
}

figure > figcaption {
  text-align: center;
}

table {
  border-collapse: collapse;
  border: 1px solid #000;
  margin: .5em 0;
}

table td, table th {
